import api from "@constants/api";
import Service from "@services/request";

export const getData = (params) => {
  return async (dispatch) => {
    await Service.send({
      method: api.LIST_USER_CERTIFICATE.method,
      path: api.LIST_USER_CERTIFICATE.path,
      data: params,
    }).then((response) => {
      if (response?.statusCode === 200) {
        dispatch({
          type: "RESET_DATA_USER_CERTIFICATE",
        });

        dispatch({
          type: "GET_DATA_USER_CERTIFICATE",
          data: response?.data?.data,
          totalPages: response?.data?.total,
          params,
        });
      }
    });
  };
};

export const getDataExport = (params) => {
  return async (dispatch) => {
    await Service.send({
      method: api.LIST_USER_CERTIFICATE.method,
      path: api.LIST_USER_CERTIFICATE.path,
      data: params,
    }).then((response) => {
      if (response?.statusCode === 200) {
        dispatch({
          type: "GET_DATA_EXPORT_USER_CERTIFICATE",
          data: response?.data?.data,
          totalPages: response?.data?.total,
          params,
        });
      }
    });
  };
};

export const getById = (data) => {
  return async (dispatch) => {
    await Service.send({
      method: api.GET_PRODUCT.method,
      path: api.GET_PRODUCT.path,
      query: data,
    })
      .then((response) => {
        dispatch({
          type: "GET_PRODUCT",
          selected: response?.data,
        });
      })
      .catch((err) => {
        console.log(err);
      });
  };
};
export const getUserCertificate = (id, lang) => {
  return async (dispatch) => {
    await Service.send({
      method: api.GET_USER_CERTIFICATE_DETAIL.method,
      path: api.GET_USER_CERTIFICATE_DETAIL.path,
      query: { id },
    })
      .then(async (response) => {
        await Service.send({
          method: api.GET_PRODUCT.method,
          path: api.GET_PRODUCT.path,
          query: { id: response?.data?.productId, lang: lang },
        }).then((resProduct) => {
          dispatch({
            type: "GET_USER_CERTIFICATE",
            selected: {
              ...response?.data,

              product: resProduct?.data,
            },
          });
        });
      })
      .catch((err) => console.log(err));
  };
};

export const addUserCertificate = (data) => {
  return async (dispatch, getState) => {
    await Service.send({
      method: api.CREATE_USER_CERTIFICATE.method,
      path: api.CREATE_USER_CERTIFICATE.path,
      data: data,
    })
      .then((response) => {
        dispatch({
          type: "ADD_USER_CERTIFICATE",
          response,
        });
      })
      .catch((err) =>
        dispatch({
          type: "ADD_USER_CERTIFICATE",
          err,
        })
      );
  };
};

export const updateUserCertificate = (data) => {
  return async (dispatch, getState) => {
    await Service.send({
      method: api.UPDATE_USER_CERTIFICATE.method,
      path: api.UPDATE_USER_CERTIFICATE.path,
      data: data,
    })
      .then((response) => {
        dispatch({
          type: "UPDATE_USER_CERTIFICATE",
          response,
        });
      })

      .catch((err) =>
        dispatch({
          type: "UPDATE_USER_CERTIFICATE",
          err,
        })
      );
  };
};
