import { useEffect, useState } from "react";
import { isObjEmpty } from "@utils";
import { FormattedMessage, injectIntl } from "react-intl";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import "react-phone-number-input/style.css";
import avatarBlank from "./../../../../../assets/images/avatars/bg-blank.png";
import classnames from "classnames";
import { Controller, useForm } from "react-hook-form";
import { convertFileToBase64, uploadImage } from "../../../../../helper/common";
import { useDispatch, useSelector } from "react-redux";
import { useHistory, useParams } from "react-router-dom";
import {
  Button,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Media,
  Row,
} from "reactstrap";
import validateOptions from "@constants/validate";
import { getById, updateUserCertificate } from "../store/action";
import classNames from "classnames";
import Select from "react-select";

const KindTab = ({ selected, intl }) => {
  const store = useSelector((state) => state.user_certificate);
  const { productDetail } = useSelector((state) => state.user_certificate);
  const product = useSelector((state) => state.products);
  const student = useSelector((state) => state.users);
  const certificate = useSelector((state) => state.certificate);
  const [changeCertificate, setChangeCertificate] = useState();
  const history = useHistory();
  const [userCertificateData, setUserCertificateData] = useState(null);
  const lang = useSelector((state) => state.common.language);
  const dispatch = useDispatch();
  const [disable, setDisable] = useState(false);
  const { id } = useParams();
  const UserCertificateOptions = validateOptions.UserCertificateOptions;
  const [changeSelect, setChangeSelect] = useState();
  const [fileImage, setFileImage] = useState();
  const [avatar, setAvatar] = useState(avatarBlank);
  const statusOptions = [
    // { value: 0, label: <FormattedMessage id="Deactive" /> },
    { value: 1, label: <FormattedMessage id="Active" /> },
    { value: 0, label: <FormattedMessage id="Blocked" /> },
  ];
  
  const [selectStatus, setSelectStatus] = useState(null);
  const [selectProduct, setSelectProduct] = useState(null);
  const [selectStudent, setSelectStudent] = useState(null);

  const cate = yup.object({
    productId: yup
      .number()
      .required(<FormattedMessage id="The course field is required" />),
    usersId: yup
      .number()
      .required(
        <FormattedMessage id="Select the student who wants to issue the certificate" />
      ),

    certificateId: yup
      .string()
      .required(<FormattedMessage id="The certifycate field is required" />),
  });

  const onChange = async (e) => {
    const imageBase64 = await convertFileToBase64(e.target.files[0]);
    setFileImage(e.target.files[0]);
    setAvatar(imageBase64);
  };

  useEffect(() => {
    if (userCertificateData) {
      dispatch(
        getById({
          id: changeCertificate
            ? parseInt(changeCertificate)
            : userCertificateData?.productId,

          lang: lang,
        })
      );
    }
  }, [changeCertificate, dispatch, lang, userCertificateData]);
  const {
    register,
    formState: { errors },
    handleSubmit,
    control,
    setError,
    setValue,
  } = useForm({
    resolver: yupResolver(cate),
    mode: "all",
  });

  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err]);
  useEffect(() => {
    if (
      selected !== null ||
      (selected !== null &&
        userCertificateData !== null &&
        selected.id !== userCertificateData.id)
    ) {
      setUserCertificateData(selected);
      setValue("productId", selected?.product?.id);
      setValue("usersId", selected?.user?.id);
      setValue("certificateId", selected?.certificate?.id);
      setAvatar(selected?.stamp);
      setValue("status", selected?.status);
      setValue("qr_code", selected?.qr_code);

      setSelectProduct({
        value: selected?.product?.id,
        label: selected?.product?.product_names[0]?.name,
      });

      setSelectStudent({
        value: selected?.user?.id,
        label: selected?.user?.first_name + " " + selected?.user?.last_name,
      });
      setSelectStatus(statusOptions[selected?.status]);
    }
  }, [selected, userCertificateData]);

  useEffect(() => {
    if (store.status === 200) {
      history.push("/apps/achievements/certificate/list");
    }
  }, [store.status, history]);

  const onSubmit = async (values) => {
    if (isObjEmpty(errors)) {
      setDisable(true);
      var urlImage = " ";
      if (avatar) {
        const newData = avatar.replace(/,/gi, "").split("base64");
        if (newData[1]) {
          const data = {
            imageData: newData[1],
            imageFormat: fileImage.type.split("/")[1],
          };
          await uploadImage(data, fileImage).then((res) => {
            urlImage = res?.data;
          });
        } else {
          urlImage = avatar;
        }
        if (urlImage) {
          dispatch(
            updateUserCertificate({
              id: parseInt(id),
              data: {
                productId: values.productId,
                certificateId: productDetail?.certificate?.id
                  ? productDetail?.certificate?.id
                  : "",
                lang: lang,
                stamp: urlImage,
                status: values.status,
                usersId: values.usersId,
              },
            })
          );
        }
      }
    }
  };

  return (
    <Row>
      <Col sm="12">
        <Media className="mb-2">
          <Media className="mt-50" body>
            <h4>{selected.name} </h4>
          </Media>
        </Media>
      </Col>
      <Col sm="12">
        <Form onSubmit={handleSubmit(onSubmit)}>
          <FormGroup>
            <Label>
              <FormattedMessage id="Course" />
              <span className="text-danger"> * </span>
            </Label>

            {product?.data?.length > 0 && selectProduct && (
              <Controller
                control={control}
                name="productId"
                render={({ field }) => {
                  return (
                    <Select
                      id="productId"
                      innerRef={register}
                      name="productId"
                      placeholder={<FormattedMessage id="Select Course" />}
                      defaultValue={selectProduct}
                      className={classnames(
                        "react-select",
                        !changeSelect
                          ? {
                              "is-invalid": errors["productId"],
                            }
                          : ""
                      )}
                      options={product?.data?.map((item, index) => {
                        return {
                          value: item?.id,
                          label: item.product_names[0].name || "",
                        };
                      })}
                      classNamePrefix="select"
                      {...field}
                      onChange={(e) => {
                        setError("productId", "");
                        setValue("productId", e?.value);
                        setChangeSelect(e?.value);
                        setChangeCertificate(e?.value);
                      }}
                    />
                  );
                }}
              ></Controller>
            )}

            <small className="text-danger">
              {errors?.productId && errors.productId.message}
            </small>
          </FormGroup>
          <FormGroup>
            <Label for="certificateId">
              <FormattedMessage id="certificate" />
              <span className="text-danger"> * </span>
            </Label>
            <Input
              name="certificateId"
              id="certificateId"
              placeholder=""
              value={
                productDetail?.certificate?.id
                  ? productDetail?.certificate?.title
                  : ""
              }
              readOnly={true}
              innerRef={register(UserCertificateOptions.certificateId)}
              className={
                productDetail?.certificate
                  ? ""
                  : classnames({ "is-invalid": errors["certificateId"] })
              }
            />

            {productDetail?.certificate == null && (
              <small className="text-danger">
                {errors?.certificateId && errors.certificateId.message}
              </small>
            )}
          </FormGroup>
          <FormGroup>
            <Label>
              <FormattedMessage id="Student" />
              <span className="text-danger"> * </span>
            </Label>

            {student?.data?.length > 0 && selectStudent && (
              <Controller
                control={control}
                name="usersId"
                render={({ field }) => {
                  return (
                    <Select
                      id="usersId"
                      innerRef={register}
                      name="usersId"
                      placeholder={<FormattedMessage id="Select Student" />}
                      defaultValue={selectStudent}
                      className={classnames(
                        "react-select",
                        !changeSelect
                          ? {
                              "is-invalid": errors["usersId"],
                            }
                          : ""
                      )}
                      options={student?.data?.map((item, index) => {
                        return {
                          value: item?.id,
                          label: item.first_name + " " + item.last_name || "",
                        };
                      })}
                      classNamePrefix="select"
                      {...field}
                      onChange={(e) => {
                        setError("usersId", "");
                        setValue("usersId", e?.value);
                        setChangeSelect(e?.value);
                      }}
                    />
                  );
                }}
              ></Controller>
            )}

            <small className="text-danger">
              {errors?.usersId && errors.usersId.message}
            </small>
          </FormGroup>

          <FormGroup>
            <Label>
              <FormattedMessage id="status" />
              <span className="text-danger"> * </span>
            </Label>
            {statusOptions?.length > 0 && selectStatus && (
              <Controller
                control={control}
                name="status"
                render={({ field }) => {
                  return (
                    <Select
                      id="status"
                      innerRef={register}
                      name="status"
                      placeholder={<FormattedMessage id="Select status" />}
                      options={statusOptions}
                      defaultValue={selectStatus}
                      classNamePrefix="select"
                      {...field}
                      onChange={(e) => {
                        setError("status", "");
                        setValue("status", e?.value);
                        setChangeSelect(e?.value);
                        setSelectStatus(e);
                      }}
                    />
                  );
                }}
              ></Controller>
            )}
          </FormGroup>

          <FormGroup>
            <Label for="Stamp">
              <FormattedMessage id="Photo of seal and signature" />
            </Label>
            <Row className="align-items-end p-1">
              <Media className="mr-25" left>
                <Media
                  object
                  className="rounded mr-50 objectFit-contain"
                  src={avatar}
                  height="100"
                  width="100"
                />
              </Media>{" "}
              <Row className="flex-column px-1">
                <Button.Ripple tag={Label} className="mt-1" color="primary">
                  <FormattedMessage id="Upload" />
                  <Input
                    type="file"
                    onChange={(e) => onChange(e)}
                    hidden
                    accept="image/*"
                  />
                </Button.Ripple>
              </Row>
            </Row>
          </FormGroup>

          <div style={{ textAlign: "end" }}>
            <Button
              type="submit"
              className="mr-1"
              color="primary"
              disabled={disable}
            >
              <FormattedMessage id="update" />
            </Button>
            <Button
              type="reset"
              color="secondary"
              outline
              onClick={() => history.goBack()}
            >
              <FormattedMessage id="Cancel" />
            </Button>
          </div>
        </Form>
      </Col>
    </Row>
  );
};
export default injectIntl(KindTab);
