import { useEffect, useState } from "react";
import { AlignJustify, Award, Pocket } from "react-feather";
import { FormattedMessage } from "react-intl";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import {
  Alert,
  Card,
  CardBody,
  Col,
  Nav,
  NavItem,
  NavLink,
  Row,
  TabContent,
  TabPane,
} from "reactstrap";
import { getUserCertificate } from "../store/action";
import CertificateTab from "./Certificate";
import { getData as getDataProduct } from "../../../product/store/action";
import { getData as getDataStudent } from "../../../user/store/action";
import { getData as getDataCertigicate } from "../../../course/certificate/store/action";

const KindEdit = () => {
  const [activeTab, setActiveTab] = useState("1"),
    store = useSelector((state) => state.user_certificate),
    dispatch = useDispatch(),
    { id } = useParams();
  const lang = useSelector((state) => state.common.language);

  const toggle = (tab) => setActiveTab(tab);

  useEffect(() => {
    dispatch(getUserCertificate(parseInt(id), lang));
  }, [dispatch]);

  useEffect(() => {
    dispatch(
      getDataCertigicate({
        filter: {
          status: "1",
        },
        // skip: 0,
        // limit: 20,
        order: [
          {
            key: "id",
            value: "asc",
          },
        ],
      })
    );
  }, [lang]);

  useEffect(() => {
    dispatch(
      getDataProduct({
        filter: {
          status: 1,
          lang,
          product_type: 0,
        },
        // skip: 0,
        // limit: 1000,
        order: [
          {
            key: "id",
            value: "asc",
          },
        ],
      })
    );
  }, [lang]);

  useEffect(() => {
    dispatch(
      getDataStudent({
        filter: {},
        // skip: 0,
        // limit: 20,
        order: {
          key: "id",
          value: "asc",
        },
      })
    );
  }, [lang]);

  return store.selected !== null && store.selected !== undefined ? (
    <Row className="app-user-edit">
      <Col sm="12">
        <Card>
          <CardBody className="pt-2">
            <Nav pills>
              <NavItem>
                <NavLink active={activeTab === "1"} onClick={() => toggle("1")}>
                  <Pocket size={14} />
                  <span className="align-middle d-none d-sm-block">
                    <FormattedMessage id="Certificate" />
                  </span>
                </NavLink>
              </NavItem>
            </Nav>
            <TabContent activeTab={activeTab}>
              <TabPane tabId="1">
                <CertificateTab selected={store.selected} />
              </TabPane>
            </TabContent>
          </CardBody>
        </Card>
      </Col>
    </Row>
  ) : (
    <Alert color="danger">
      <h4 className="alert-heading">Not found</h4>
    </Alert>
  );
};
export default KindEdit;
